@media (max-width: 1200px) {
	header {
		nav {
			padding: 13px 0 5px;
			.menu {
				padding: 0;
				&_item {
					padding-right: 30px;
					&:last-child {
						padding: 0; } }
				&_link {
					font-size: 12px; } } }
		.subheader {
			padding-top: 58px;
			&_logo {
				margin-top: 15px; }
			&_btn {
				width: 75%; }
			&_call, &_phone {
				text-align: center; }
			&_phone {
				font-size: 19px; } } }

	.promo {
		&_header {
			font-size: 50px; } }

	.mobile {
		&_item {
			width: 290px;
			&_subtitle {
				font-size: 22px; } } }
	.footer {
		flex-wrap: wrap;
		height: auto;
		justify-content: center;
		&_social {
			margin: 20px;
			order: 2;
			width: 100%; }
		&_nav {
			left: initial;
			order: 1;
			position: static;
			transform: none; }
		&_apps {
			order: 3; } } }

@media (max-width: 991px) {
	header {
		nav {
			.menu {
				&_item {
					padding-right: 18px; }
				&_link {
					font-size: 10px; } } }
		.subheader {
			&_btn {
				padding: 12px 0;
				width: 100%; } } }

	.promo {
		.container {
			max-width: 100%; }
		&_header {
			font-size: 40px; }
		&_subheader {
			font-size: 30px; } }

	.advantages {
		.container {
			.col-md-6 {
				.container {
					min-height: 340px; }
				.row {
					align-items: center;
					flex-direction: column;
					.col-md-4, .col-md-8 {
						max-width: 100%; }
					.col-md-8 {
						margin-top: 20px; } }
				&:last-of-type {
					.container:last-of-type {
						.advantages_img {
							margin: 15px auto 0; } } } } } }

	.mobile {
			&_item {
				width: auto; } } }

@media (max-width: 767px) {
	header {
		nav {
			padding: 13px;
			.menu {
				align-items: center;
				background: rgba(0,0,0, .8);
				flex-direction: column;
				height: 100vh;
				justify-content: center;
				left: -100%;
				padding: 20px 15px;
				position: fixed;
				top: 0;
				transition: .5s;
				width: 50%;
				&_active {
					left: 0; }
				&_item {
					border: none;
					margin: 10px 0;
					padding-right: 0; }
				&_link {
					font-size: 20px; } } }
		.subheader {
			padding-top: 26px;
			&_phone {
				font-size: 16px;
				margin: 6px 0 2px; }
			&_btn {
				margin: 8px 0 12px;
				padding: 8px 0; }
			.xs-hidden {
				display: none; }
			.xs-visible {
				display: block; } }

		.hamburger {
			display: block;
			height: 12px;
			position: absolute;
			right: 10px;
			top: 5.5%;
			width: 25px;
			z-index: 2;
			span {
				background: #fff;
				display: block;
				height: 2px;
				margin-bottom: 2px;
				transition: .5s;
				width: 100%; }
			&_active {
				span {
					margin-bottom: -2px;
					&:nth-child(1) {
						transform: translateY(3px) rotate(-45deg); }
					&:nth-child(2) {
						display: none; }
					&:nth-child(3) {
						transform: translateY(3px) rotate(45deg); } } } } }

	.promo {
		margin-top: -139px;
		padding-top: 210px;
		&_subheader {
			margin: 20px -4px; }
		&_description {
			p {
				margin-bottom: 20px; } } } }

@media (max-width: 575px) {
	header {
		nav {
			.menu {
				&_item {
					text-align: center;
					padding: 5px 0;
					width: 100%; }
				&_link {
					display: block; } }
			.menu_active {
				width: 85%; } }
		.subheader {
			.row {
				.col-6 {
					&:last-of-type {
						padding-left: 11vw; } } }
			&_logo {
				margin-top: 11px; }
			&_phone {
				font-size: 12px;
				font-weight: 400;
				margin: 8px 0 -3px 12px;
				text-align: left; }
			&_btn {
				border-bottom: solid 0.5px rgba(0, 0, 0, 0.07);
				font: 500 10px Roboto;
				margin: 8px 0;
				padding: 4px 9px;
				width: fit-content;
				&:hover {
					background: linear-gradient(to top, #66afcA, #8ce1fd); } } } }

	.promo {
		margin-top: -68px;
		padding: 100px 0 35px;
		&_header {
			font: 900 30px Roboto; }
		&_subheader {
			font: 600 20px Roboto; }
		&_description {
			display: none; }
		&_btn {
			margin-top: 50px;
			padding: 15px 25px;
			&:hover {
				background: linear-gradient(to top, #66afcA, #8ce1fd); } } }

	.advantages {
		@include padding_section;
		@include padding_title;
		.container {
			margin-bottom: 48px;
			.title + .container {
				margin-top: 6px; }
			.col-md-6 {
				.container {
					min-height: auto; }
				&:last-child {
					.container {
						&:last-child {
							margin-bottom: 0; } } } } }
		&_img {
			height: 100px;
			width: 100px; }
		&_text {
			margin: 0 -25px; }
		&_title {
			font-size: 16px; } }

	.mobile {
		@include padding_section;
		@include padding_title;
		@include subtitle_font;
		.subtitle {
			text-shadow: 0 0 .9px; }
		&_item {
			margin-bottom: 30px;
			&_subtitle {
				font: 400 18px/1.72 Roboto;
				@include shadow_text; } }
		.col-md-6:last-of-type, .col-lg-4:last-of-type {
			.mobile_item {
				margin-bottom: 43px; } } }

	.choice {
		background: #070716;
		@include padding_section;
		&_curtain, .title, .subtitle {
			display: none; }
		&_car {
			background: url(../img/car.png) no-repeat center top/contain;
			height: 190px;
			margin: 35px 0 -10px; }
		&_description {
			color: #fff;
			margin-bottom: 30px; } }
	.require {
		@include padding_section;
		@include padding_title;
		&_block {
			margin-top: 14px; }
		&_title {
			font-size: 16px; }
		&_list {
			li {
				font: 300 14px/1.2 Roboto;
				&:before {
					top: 0; }
				@include shadow_text; } }
		.col-md-6 {
			&:last-child {
				.require_block {
					margin-bottom: 23px; } } } }

	.world {
		@include padding_section;
		@include padding_title;
		@include subtitle_font;
		height: 360px;
		&_look {
			bottom: 26px;
			left: 0;
			transform: none;
			width: 100%; } }

	.footer {
		border: none;
		padding: 25px 0 15px;
		&_nav {
			.footer_main_menu {
				padding-top: 21px;
				&_list {
					li {
						margin: 0 7px; } } }
			.footer_sub_menu {
				&_list {
					flex-wrap: wrap;
					justify-content: space-between;
					li {
						margin: 5px 11px; } }
				margin: 7px 0 5px;
				padding: 0 35px;
				a {
					color: #b4b4b4;
					text-shadow: 0 0 1.1px; } } }
		&_social {
			margin: 17px 0 20px; } } }
