.label, .title {
	text-align: center;
	text-transform: uppercase; }

.label {
	background: #000;
	color: rgba(255, 255, 255, 0.8);
	font: 400 11px Roboto;
	margin: 0 auto;
	padding: 4px 11px 5px;
	width: fit-content; }

.title {
	color: #222;
	font: 600 38px Roboto;
	margin: 34px 0 0 0;
	text-shadow: 0 0 0.8px; }

.subtitle {
	color: #222222;
	font: 400 17px Roboto;
	margin-top: 10px;
	text-align: center; }

a:hover {
	text-decoration: none; }

/* --------- Стили после переменных и универсальных блоков ----------- */
header {
	color: #fff;
	position: relative;

	nav {
		background: #212121;
		padding: 32px 0 15px;
		position: fixed;
		width: 100%;
		z-index: 1;

		.menu {
			display: flex;
			justify-content: space-between;
			list-style: none;

			&_item {
				border-right: 2px dotted #000;
				padding-right: 33px;
				&:last-child {
					border-right: none; } }

			&_link {
				color: #fff;
				font: 300 15px Roboto;
				@include a_hover; } } } }

.subheader {
	background: rgba(0, 0, 0, 0.6);
	padding-top: 87px;

	&_logo {
		display: block;
		margin-top: 16px;
		max-width: 100%;
		img {
			max-width: 100%; } }

	&_call {
		font: 300 12px Roboto;
		margin-top: 20px;
		text-align: right; }

	&_phone {
		color: #fff;
		display: block;
		font: 300 22px Roboto;
		margin-top: 2px;
		text-align: right; }

	&_btn {
		background: linear-gradient(to top, #66afcA, #8ce1fd);
		border: none;
		border-radius: 4px;
		box-shadow: inset 0px -3px 0 0 rgba(0, 0, 0, 0.12);
		color: #fff;
		cursor: pointer;
		font: 300 14px Roboto;
		margin: 19px 0;
		padding: 15px 0;
		text-align: center;
		text-shadow: 0.7px 0.7px 0 rgba(0, 0, 0, 0.21);
		text-transform: uppercase;
		transition: .5s;
		width: 100%;
		@include btn_hover; }

	.xs-visible {
		display: none; } }

.hamburger {
	display: none; }

/* --------- Приглашение водителей ----------- */

.promo {
	background: url("../img/landing-car.jpg") no-repeat center (center / cover);
	color: #fff;
	margin-top: -85px;
	padding: 148px 0 135px;
	text-align: center;

	&_header {
		font: 900 60px Roboto;
		margin: 0;
		@include shadow_text; }

	&_subheader {
		font: 600 36px Roboto; }

	&_description {
		font: 400 18px/1.45 Roboto;

		p {
			margin-bottom: 25px; } }

	&_btn {
		background: linear-gradient(to top, #66afcA, #8ce1fd);
		border: none;
		border-radius: 4px;
		box-shadow: inset 0px -3px 0 0 rgba(0, 0, 0, 0.12);
		color: #fff;
		cursor: pointer;
		font: 400 18px Roboto;
		margin-top: 90px;
		padding: 22px 39px;
		text-align: center;
		text-shadow: 0.7px 0.7px 0 rgba(0, 0, 0, 0.21);
		text-transform: uppercase;
		transition: .5s;
		@include btn_hover; } }

/* --------- Преимущества ----------- */

.col-md-6 .container {
	margin-bottom: 29px;
	min-height: 190px; }

.advantages_header + .container {
	text-transform: none; }

.advantages {
	padding: 71px 0 84px;
	.container {
		.col-md-6:last-of-type {
			.container:last-of-type {
				.advantages_img {
					margin-top: 0; } } }
		.title + .container {
			margin-top: 37px; } }
	.col-md-6 {
		.row {
			align-items: flex-start; } }
	.container {}

	&_img {
		align-items: center;
		background: $causes-conditions;
		border-radius: 50%;
		display: flex;
		margin: 15px auto 0;
		height: 113px;
		justify-content: center;
		width: 113px; }
	&_text {
		color: #202020;
		font: 300 15px/1.4 Roboto;
		text-align: justify;
		@include shadow_text; }
	&_title {
		color: $causes-conditions;
		font: 400 17px/1.3 Roboto;
		margin-bottom: 1px;
		text-align: left; } }

/* --------- Преимущества ----------- */

.mobile {
	background: #1eacc7 url("../img/mobile.jpg") no-repeat center center/cover;
	padding: 48px 0 80px;
	.row {
		margin-top: 29px; }
	&_item {
		background: rgba(7, 7, 21, .9) no-repeat center center/80%;
		background-blend-mode: multiply;
		height: 280px;
		margin: 0 auto 19px;
		opacity: .9;
		padding: 88px 20px 132px;
		position: relative;
		width: 320px;
		&_1 {
			background-image: url("../img/mobile/create_order.png"); }
		&_2 {
			background-image: url("../img/mobile/reliable_feed.png"); }
		&_3 {
			background-image: url("../img/mobile/transparent_prices.png"); }
		&_4 {
			background-image: url("../img/mobile/cashless_payment.png"); }
		&_5 {
			background-image: url("../img/mobile/feedback.png"); }
		&_6 {
			background-image: url("../img/mobile/divide_cost.png"); }
		&_subtitle {
			color: #fff;
			font: 400 24px/1.29 Roboto;
			text-align: center;
			text-transform: uppercase; }
		&_plus {
			align-items: center;
			background: #000;
			border-radius: 50%;
			bottom: 23px;
			cursor: pointer;
			display: flex;
			height: 26px;
			justify-content: center;
			left: 50%;
			margin: 0 auto;
			position: absolute;
			transform: translateX(-50%);
			width: 26px;
			&:before {
				background: $causes-conditions;
				content: "";
				display: block;
				height: 2px;
				transition: .3s;
				width: 14px; }
			&:after {
				background: $causes-conditions;
				content: "";
				display: block;
				height: 14px;
				left: 50%;
				position: absolute;
				transform: translateX(-50%);
				transition: .3s;
				width: 2px; }
			&:hover {
				&:before, &:after {
					background: $requirements-documents; } } } } }

/* --------- Выбор машины ----------- */

.choice {
	background: #1eacc7 url("../img/mobile.jpg") no-repeat center center/cover;
	padding: 75px 0 135px;
	position: relative;
	.container {
		position: relative; }
	&_curtain {
		background: #070716;
		height: 55.7%;
		left: 0;
		position: absolute;
		top: 0;
		width: 100%; }

	.label {
		background: #fff;
		color: #070716; }
	.title, .subtitle {
		color: #fff; }
	&_car {
		background: url("../img/car.png") no-repeat center center/100%;
		height: 480px;
		margin-top: 50px;
		width: 100%; }
	&_description {
		color: #222;
		font: 355 17px Roboto;
		text-align: center;
		span {
			display: block;
			padding: 2px 0; } } }

/* --------- Условия и требования ----------- */

.require {
	padding: 68px 0 80px;
	&_block {
		margin: 16px 0;
		&:first-of-type .require_list {
			margin: 11px 0 5px;
			li:last-of-type {
				margin-bottom: 0; } } }
	&_title {
		color: #1ca8c3;
		font: 400 22px Roboto; }
	&_list {
		list-style: none;
		margin-top: 11px;
		padding-left: 50px;
		li {
			color: #222;
			font: 300 17px/1.41 Roboto;
			margin: 9px 0;
			position: relative;
			&:before {
				background: url("../icons/condition.svg") no-repeat center center/cover;
				content: "";
				display: block;
				height: 20px;
				left: -32px;
				position: absolute;
				top: 3px;
				vertical-align: middle;
				width: 21px; } } }
	&_description {
		color: #1ca8c3; }
	.warning {
		margin-bottom: 0;
		.require_title {
			color: $requirements-documents; }
		.require_list {
			li {
				&:before {
					background: url("../icons/requirement.svg") no-repeat center center/cover; } } } } }

/* --------- По всему миру ----------- */

.world {
	background: url("../img/map.png") no-repeat center center/cover;
	height: 717px;
	padding-top: 49px;
	position: relative;
	.label {
		background: #fff;
		color: #070716; }
	.title, .subtitle {
		color: #fff; }
	&_look {
		bottom: 49px;
		color: #1fbad6;
		font: 400 15px Roboto;
		left: 50%;
		position: absolute;
		text-align: center;
		text-transform: uppercase;
		transform: translateX(-50%); } }

/* --------- Подвал ----------- */

.footer {
	align-items: center;
	border-bottom: 8px solid #070716;
	display: flex;
	height: 156px;
	justify-content: space-between;
	padding: 14px 56px 27px 23px;
	position: relative;
	&_social {
		&_list {
			@include list;
			li {
				background: #070716;
				border-radius: 50%;
				cursor: pointer;
				height: 32px;
				margin: 0 4.5px;
				position: relative;
				width: 32px;
				a {
					display: block;
					&:hover {
						&:before {
							color: #007bff; } }
					&:before {
						color: #fff;
						font: 300 18px FontAwesome;
						left: 50%;
						position: absolute;
						top: 50%;
						transform: translate(-50%, -50%);
						transition: .5s; } }
				&:nth-child(1) {
					a {
						&:before {
							content: "\f099"; } } }
				&:nth-child(2) {
					a {
						&:before {
							content: "\f098"; } } }
				&:nth-child(3) {
					a {
						&:before {
							content: "\f08c"; } } }
				&:nth-child(4) {
					a {
						&:before {
							content: "\f16d"; } } } } } }
	&_nav {
		font: 300 11px Roboto;
		left: 50%;
		position: absolute;
		transform: translateX(-50%);
		.footer_main_menu {
			padding-top: 32px;
			position: relative;
			&:before {
				background: #1fbad6;
				content: "";
				height: 4px;
				left: 50%;
				position: absolute;
				top: 0;
				transform: translateX(-50%);
				width: 299px; }
			&_list {
				@include list;
				font: 400 15px Roboto;
				li {
					margin: 0 24px; }
				a {
					@include a_hover;
					color: #070716; } } }
		.footer_sub_menu {
			margin: 12px 0 18px;
			&_list {
				@include list;
				li {
					margin: 0 11px; }
				a {
					@include a_hover;
					color: #070716; } } }
		.footer_language {
			font-weight: 400;
			text-align: center;
			text-transform: uppercase;
			a {
				color: #1fbad6; } } }
	&_apps {
		&_list {
			@include list;
			li {
				cursor: pointer;
				margin: 0 3px;
				&:nth-child(1) {
					a {
						background-image: url("../icons/app-store.png"); } }
				&:nth-child(2) {
					a {
						background-image: url("../icons/google-play.png"); } }
				&:nth-child(3) {
					a {
						background-image: url("../icons/windows-store.png"); } }
				a {
					background: no-repeat center center/cover;
					display: block;
					height: 26px;
					width: 94px; } } } } }







