@mixin btn_hover {
	&:hover {
		background: linear-gradient(to top, $causes-conditions, #8ce1fd); } }

@mixin a_hover {
	&:hover {
		color: $causes-conditions; } }

@mixin list {
	display: flex;
	justify-content: center;
	list-style: none;
	margin: 0;
	padding: 0;
	text-transform: uppercase; }

@mixin shadow_text {
	text-shadow: 0 0 1.3px; }
@mixin padding_section {
	padding: 22px 0 0 0; }
@mixin padding_title {
	.title {
		font-size: 16px;
		margin: 10px -2px 0; } }
@mixin subtitle_font {
	.subtitle {
		font-size: 14px;
		@include shadow_text; } }
